
.nav-container {
    overflow: hidden;
    background-color: #f1cc64;
 
    display: block;
    color: black;
    text-align: center;
    padding: 10px;
    font-weight: bold;
    font-style: italic;
    color:#8d6700;
    font-size: 17px;
    width: 100%;
    margin: 0;
    
   
  }
  

  @media screen and (max-width: 600px) {
    .nav-container {
      float: none;
      display: block;
      text-align: center;
      width: 100%;
      margin: 0;
      padding: 14px;
    }
    
  }
.search-container{
    
    display: block;
    color: #8d6700;
    text-align: center;
    padding: 16px ;
    
    text-decoration: none;
    font-size: 17px;
    background-image: url(https://www.grainchain.com/sites/default/files/flour-baking-background-with-egg%2C-rolling-pin-and-wheat-ear-000069728007_Medium_3.jpg);
    background-repeat: no-repeat;
    background-size: 100% ;
}
.search-container input[type=text] {

    margin-top: 8px;
    font-size: 17px;
    font-weight: bold;
    border: 1px solid #8d6700;
    border-radius: 5px;
    width: 15%;
    text-align: center;
  }
  .search-container ::-webkit-input-placeholder{
      color: #5e480b;
      padding-left: 10px;
  }
  .search-container ::-ms-input-placeholder{
      color: #5e480b;
      padding-left: 10px;
  }
  .search-container ::placeholder{
      color: #5e480b;
      padding-left: 10px;
  }
   .search-container button {

    padding: 15px 26px ;
    border-radius: 5px;
    margin-top: 8px;
    margin-right: 16px;
    background: #ac820e;
    font-size: 17px;
    font-weight: bold;
    border: none;
    cursor: pointer;

  }

  @media screen and (max-width: 600px) {
    .search-container .search-container {
      float: none;
    }
    .search-container a, .search-container input[type=text], .search .search-container button {
      float: none;
      text-align: center;
      width: 100%;
      margin: 0;
      margin-top: 10px;
     
    }
    .search-container input[type=text] {
      border: 1px solid #8d6700;  
    }
  }
  
   .search-container button:hover {
    background: #8d6700;
    
  }
